import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

import Login from '@/views/Login'
import Main from '@/views/Main'
import AppIndex from '@/views/AppIndex'

Vue.use(Router)

export default new Router({
  // 不加#访问
  mode: 'history',
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/main',
      name: 'Main',
      component: Main
    },
    {
      path: '/index',
      name: 'AppIndex',
      component: AppIndex
    }
  ]
})



